.metamask-fox {
    width: 500px;
    height: 500px;
    position: absolute;
    top:20%;
    left:20%;
    
  }
  .metamask-fox:hover {
    -webkit-animation: fox-jump 1s infinite;
            animation: fox-jump 1s infinite;
  }
  @-webkit-keyframes fox-jump {
    0% {
      -webkit-transform: translateY(0);
              transform: translateY(0);
    }
    50% {
      -webkit-transform: translateY(-30px);
              transform: translateY(-30px);
    }
    100% {
      -webkit-transform: translateY(0);
              transform: translateY(0);
    }
  }
  @keyframes fox-jump {
    0% {
      -webkit-transform: translateY(0);
              transform: translateY(0);
    }
    50% {
      -webkit-transform: translateY(-30px);
              transform: translateY(-30px);
    }
    100% {
      -webkit-transform: translateY(0);
              transform: translateY(0);
    }
  }
  
