.imageNft {
 
  width: -webkit-fit-content;
 
  width: -moz-fit-content;
 
  width: fit-content;
  height: 300px;
  background-color: rgb(29, 29, 29);
  position: relative;
  margin: auto;
  margin-bottom: 10px;
  border-radius: 10px;
  font-family: "Source Code Pro", monospace;
}

.imageNft img {
  object-fit: contain;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  height: 300px;
  font-family: "Source Code Pro", monospace;
}

.imageNft p {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  font-family: "Source Code Pro", monospace;
}

